body{
  width: 100%;
  height: 100vh;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.grid{
  font-size: 16px;
  text-align: center;
  width: 50px;
  height: 50px;
  border: 1px solid #282c34;
  color: black;
  font-weight: 600;
}
input:nth-child(3n){
  border-right:2px solid black ;
}
.grid-correct{
  background: rgba(2, 182, 2, 0.5);
}
.grid-wrong{
  background: rgb(255, 0, 0,0.5);
}
.grid:read-only{
  background-color: rgb(216, 216, 216,0.5);
}
button{
  padding: 10px;
  margin: 20px 10px 10px;
  background: #202020;
  border: none;
  border-radius: 3px;
  color: white;
  font-weight: 600;
}